  .checkbox-group {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .checkbox-group label {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .checkbox-group input[type="checkbox"] {
    margin-right: 0.5rem;
  }
  
  ul {
    padding-left: 1.5rem;
    list-style-type: disc;
  }

    
  .price-input-container {
    margin-left: 1rem;
  }
  